.App {
  font-family: Arial, sans-serif;
  max-width: 600px;
  margin: 40px auto;
  padding: 0 20px;
}

h1 {
  text-align: center;
  margin-bottom: 30px;
}

.codes-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.code-box {
  border: 2px solid #4caf50;
  border-radius: 8px;
  padding: 15px;
  background-color: #f9fff9;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
  transition: transform 0.2s;
  position: relative;
}

.code-box:hover {
  transform: scale(1.02);
}

.code {
  font-size: 1.8rem;
  font-weight: bold;
  color: #2e7d32;
  margin-bottom: 8px;
}

.info {
  font-size: 0.9rem;
  color: #555;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.from {
  font-weight: bold;
  font-size: 1rem;
  margin-right: 1rem;
}

.date {
  position: absolute;
  top: 15px;
  right: 15px;
}

.Login {
  display: flex;
  width: 100%;
  justify-content: center;
}

.Loginbox {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  padding: 0rem 3rem 3rem 3rem;
  border-radius: 3rem;
  border: #555 solid;
}

.margintop {
  margin-top: 1rem;
}